<template>
  <el-container>
    <!-- 顶栏 -->
    <el-header height="60px">
      <el-col :span="1"><img src="/static/images/logo.png"
                             style="width: 45px; height: 45px; padding-top: 7px"/>
      </el-col>
      <el-col :span="6"><h2>智能识别垃圾分类系统</h2></el-col>
    </el-header>
    <!-- 嵌套容器 -->
    <el-container>
      <!-- 内容 -->
      <el-main>
        <div class="index"
             v-loading.fullscreen.lock="loading"
             element-loading-text="拼命加载中"
             element-loading-spinner="el-icon-loading"
             fullscreen=true
             element-loading-background="rgba(255, 255, 255, 0.5)">
          <!-- 第一列栅格布局 -->
          <el-row>
            <el-col :span="12" class="col1">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <el-button type="primary" @click="Predict">预测</el-button>
            </el-col>
          </el-row>
          <!-- 第二列布局 -->
          <el-row>
            <el-col :span="6" class="col3">
              <el-upload
                ref="upload"
                class="upload-demo"
                drag
                action="#"
                accept=".jpg, .png, .jpeg"
                :auto-upload="false"
                :on-change="uploadFile"
                list-type="picture"
                :limit=1
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div class="el-upload__tip" slot="tip">只能上传jpg/png/jpeg文件</div>
              </el-upload>
            </el-col>
            <el-col :span="1"></el-col>
            <el-col :span="12" class="col2">
              <el-card style="height:250px; box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)">
                <div slot="header" class="clearfix">
                  <span>识别结果</span>
                </div>
                <div v-for="item in result" :key="item" class="text item">
                  {{ item }}
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </el-main>
    </el-container>
    <!-- 底栏 -->
  </el-container>
</template>

<style>
.el-header {
  background-color: #67C23A;
  color: white;
}

.index {
  width: 100%;
  height: 100%;
  -moz-opacity: 0.85;
  opacity: 0.85;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: -1;
  background-image: url("/static/images/background.jpeg");
}

.el-col {
  height: 100px;
}

</style>

<script>
import requests from './utils/requests'

export default {
  name: 'loginForm',
  data () {
    return {
      options: [{
        value: '0',
        label: '全部'
      }, {
        value: '1',
        label: 'ShuffleNet'
      }, {
        value: '2',
        label: 'MobileNetV3'
      }, {
        value: '3',
        label: 'ViT'
      }, {
        value: '4',
        label: 'ResNet'
      }, {
        value: '5',
        label: 'ConvNeXt'
      }, {
        value: '6',
        label: 'Swin-Transformer'
      }],
      value: '',
      formData: null,
      result: '',
      loading: false
    }
  },
  methods: {
    uploadFile (item) {
      this.formData = new FormData()
      let file = item.raw
      this.formData.append('file', file)
      console.log(this.formData)
    },
    Predict () {
      this.loading = true
      requests({
        url: `/predict?modelType=${this.value}`,
        data: this.formData,
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        this.loading = false
        if (res.code === 200) {
          this.result = res.msg
        } else {
          this.$message.error(res.msg)
        }
      }).catch(err => {
        this.loading = false
        this.$message.error(err)
      })
    }
  }
}
</script>



